.middle_area{
    z-index: 2;
    position: absolute;
    background-color:rgba(82, 90, 86,0.3);
    border-radius: 5%;
    box-shadow: -10px 10px 40px 10px rgba(82, 90, 86,1);
    top:30%;
    left: 30%;
    width: 40%;
    height: 30%;
    -webkit-animation-duration: 10s;
    animation-duration: 10s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}
@-webkit-keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
 }
 
 @keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
 }
 
 .fadeIn {
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
 }

.middle_area .left_area,.middle_area .right_area{
    color: whitesmoke;
}
