.season-display{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 98vh;
}

.winter{
    background-color: aliceblue;
}
.summer{
    background-color:orange;
}
.spring{
    background-color: greenyellow;
}
.autumn{
    background-color: lightblue;
}

.season-display.winter i{
    color: blue;
}
.season-display.summer i{
    color: red;
}
.season-display.spring i{
    color: pink;
}
.season-display.autumn i{
    color: orange;
}
.imageList{
    display:grid;
    grid-template-columns: repeat(auto-fill,minmax(250px,1fr));
    grid-gap: 10px;
    margin: auto;
}
.imageList .imageDiv{
    width:250px;
    height: 390px;
    border: 1px black dotted;
    overflow-x: hidden;
    overflow-y: hidden;
}
.imageList .imageDiv img{
    width: 250px;
    height: 300px;
    max-height: 300px;
}
.imageList .imageDiv p{
    overflow-y: scroll;
    height: 60px;
}
.imageList .imageDiv p::-webkit-scrollbar,.imageList .imageDiv h3::-webkit-scrollbar{
    width: 0;
    background: rgba(red, green, blue, 0);
}
.imageList .imageDiv h3{
    height: 30px;
    overflow-y: scroll;
    overflow-x: hidden;
}
#root7 .border.red{
    border:1px olivedrab solid !important;
}
